@import'../common/variables';
@import'../global';

.card {
  max-width: 360px;
  width: calc(50% - 7.5px);
  &_box{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 20px 15px;
    padding: 0 20px;
  }
  &_img {
    &_box{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 15px;
      border-radius: 4px;
      background-color: #F8F8F8;
    }
  }
  .title {
    margin-bottom: 5px;
    font-size: 1.8rem;
    color: var(--c11);
    text-align: center;
  }
  .price {
    font-size: 1.6rem;
    color: var(--c77);
    text-align: center;
  }
}
